import React, { useState, useEffect } from 'react';
import MainCard from '../../../../ui-component/cards/MainCard';
import ConfirmDialog from '../../../../ui-component/ConfirmDialog/ConfirmDialog';
import { makeStyles, withStyles, useTheme } from '@material-ui/styles';
import {
    Grid,
    IconButton,
    Typography,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    InputAdornment,
    ButtonGroup
} from '@material-ui/core';

import { IconSearch } from '@tabler/icons';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import RemoveIcon from '@material-ui/icons/Remove';
import AddIcon from '@material-ui/icons/Add';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import { useDispatch, useSelector } from 'react-redux';
import { getCart, deleteBookFromCart, updateCart } from '../../../../store/actions/CartActions';
import { placeProfileOrder } from '../../../../store/actions/OrderActions';
import { useParams, useHistory } from 'react-router-dom';
import useUserRole from '../../../../hooks/useUserRole';
// import Loader from '../../../../components/Loader/Loader';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

const useStyles = makeStyles((theme) => ({
    table: {
        minWidth: 650
    },
    totalQuantityBox: {
        backgroundColor: theme.palette.primary.main,
        color: '#fff',
        fontWeight: 'bold',
        padding: '4px 8px',
        borderRadius: '8px',
        marginLeft: '16px',
    },
    tableContainer: {
        borderRadius: 15,
        marginTop: 16
    },
    tableHeaderCell: {
        fontWeight: 'lighter',
        backgroundColor: theme.palette.primary.light,
        color: '#ffffff'
    },
    status: {
        fontWeight: 'lighter',
        fontSize: '0.75rem',
        color: 'white',
        backgroundColor: 'gray',
        borderRadius: 8,
        padding: '3px 10px',
        display: 'inline-block'
    },
    root: {
        padding: 16,
        width: '100%'
    },
    btn: {
        cursor: 'pointer',
        boxShadow: '0 0.5em 0.5em 0.1em #68c2d9',
        marginLeft: 20
    },
    titleContainer: {
        background: theme.palette.primary.main,
        padding: 10,
        borderRadius: 8,
        marginBottom: 20
    },
    pageTitle: {
        fontSize: 16,
        color: theme.palette.background.paper,
        fontWeight: 'lighter'
    },

    viewIcon: {
        verticalAlign: 'sub',
        background: 'tranparent',
        color: '#fff'
    },
    searchControl: {
        width: '234px',
        marginLeft: '16px',
        paddingRight: '16px',
        paddingLeft: '16px',
        '& input': {
            background: 'transparent !important',
            paddingLeft: '5px !important'
        },
        [theme.breakpoints.down('lg')]: {
            width: '250px'
        },
        [theme.breakpoints.down('md')]: {
            width: '100%',
            marginLeft: '4px',
            background: '#fff'
        }
    },
    startAdornment: {
        fontSize: '1rem',
        color: theme.palette.grey[500]
    }
}));
export default function SchoolAdminCart() {
    const classes = useStyles();
    useUserRole({ roleList: ['ADMIN', 'SCHOOL_ADMIN','SUPER_ADMIN'] });
    const dispatch = useDispatch();
    let { id } = useParams();
    const history = useHistory();
    const [value, setValue] = useState('');
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(25);
    const [isLoading, setLoading] = useState(false);
    const [isDecreasing, setDecreasing] = useState(false);
    const { loggedUserProfile } = useSelector((state) => state?.userProfile);
    const profile_id = loggedUserProfile?.profileId;
    const { getCartData } = useSelector((state) => state?.cart);
    const [confirmDialog, setConfirmDialog] = useState({
        isOpen: false,
        title: '',
        subTitle: ''
    });
    const handleDeleteBook = async (cartId, bookId) => {
        setLoading(true);
        dispatch(
            deleteBookFromCart(cartId, bookId, () => {
                dispatch(getCart(profile_id));
                setLoading(false);
            })
        );
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        });
        setLoading(false);
    };

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
    };
    const handleIncrease = (cartId, bookId) => {
        setLoading(true);
        const payload = {
            bookId: bookId,
            quantity: 1
        };
        dispatch(
            updateCart(payload, cartId, () => {
                dispatch(getCart(profile_id));
                setLoading(false);
            })
        );
    };

    const handleDecrease = (cartId, bookId, quantity) => {
        setLoading(true);
        setDecreasing(true);
        const payload = {
            bookId: bookId,
            quantity: -1
        };
        if (quantity > 1) {
            setTimeout(() => {
                dispatch(
                    updateCart(payload, cartId, () => {
                        dispatch(getCart(profile_id));
                        setLoading(false);
                        setDecreasing(false);
                    })
                );
            }, 500);
        }
    };
    useEffect(() => {
        if (profile_id) {
            dispatch(getCart(profile_id, value ? value : '', page + 1, rowsPerPage));
        }
    }, [profile_id,value, page, rowsPerPage]);

    return (
        <>
            <MainCard>
                <div className={classes.titleContainer}>
                    <ShoppingCartIcon className={classes.viewIcon} />
                    <span className={classes.pageTitle}>&nbsp; Cart Details </span>
                </div>
                <Paper elevation={5} className={classes.root}>
                    <Grid>
                        <OutlinedInput
                            className={classes.searchControl}
                            id="input-search-header"
                            value={value}
                            onChange={(e) => setValue(e.target.value)}
                            placeholder="Search"
                            startAdornment={
                                <InputAdornment position="start">
                                    <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                </InputAdornment>
                            }
                        />
                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                        <span className={classes.totalQuantityBox}>Total Book Quantity -: {getCartData?.numberOfElements}</span>
                    </Grid>
                    <TableContainer className={classes.tableContainer}>
                        <Table className={classes.table} aria-label="simple table">
                            <TableHead className={classes.tableHeaderCell}>
                                <StyledTableRow>
                                    <TableCell> Cover </TableCell>
                                    <TableCell> Title </TableCell>
                                    <TableCell align="center"> Total Quantity </TableCell>
                                    <TableCell align="center"> Quantity </TableCell>
                                    <TableCell align="right"> Action </TableCell>
                                </StyledTableRow>
                            </TableHead>
                            {!getCartData?.content ? (
                                <caption style={{ textAlign: 'center' }}>Sorry! No data available</caption>
                            ) : (
                                <TableBody>
                                    {getCartData?.content?.map((row) => (
                                        <StyledTableRow key={row?.id}>
                                            <TableCell>
                                                <img src={row?.bookDetail?.imageUrls} alt="book image" width="60px" />
                                            </TableCell>
                                            <TableCell> {row?.bookDetail?.title} </TableCell>
                                            <TableCell align="center"> {row?.quantity >= 1 ? row?.quantity : 1} </TableCell>
                                            <TableCell align="center">
                                                <ButtonGroup color="primary" aria-label="outlined primary button group">
                                                    <Button
                                                        onClick={() => handleDecrease(row?.cartId, row?.bookDetail?.id, row?.quantity)}
                                                        disabled={row?.quantity < 2 || isDecreasing}
                                                    >
                                                        <RemoveIcon />
                                                    </Button>
                                                    <Button onClick={() => handleIncrease(row?.cartId, row?.bookDetail?.id)}>
                                                        <AddIcon />
                                                    </Button>
                                                </ButtonGroup>
                                            </TableCell>
                                            <TableCell align="right">
                                                <IconButton
                                                    onClick={() => {
                                                        setConfirmDialog({
                                                            isOpen: true,
                                                            title: 'Delete Cart',
                                                            subTitle: 'Are you sure you want to delete this book.',
                                                            onConfirm: () => {
                                                                handleDeleteBook(row?.cartId, row?.bookDetail?.id);
                                                            }
                                                        });
                                                    }}
                                                >
                                                    <DeleteForeverIcon color="error" style={{ cursor: 'pointer' }} />
                                                </IconButton>
                                            </TableCell>
                                        </StyledTableRow>
                                    ))}
                                </TableBody>
                            )}
                        </Table>
                    </TableContainer>
                    <TablePagination
                        rowsPerPageOptions={[25, 50, 100]}
                        component="div"
                        count={getCartData?.totalElements || 0}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                        sx={{
                            '.MuiTablePagination-displayedRows': {
                                marginBottom: 0
                            },
                            '.MuiTablePagination-selectLabel': {
                                marginBottom: 0
                            }
                        }}
                    />
                </Paper>

                {confirmDialog && <ConfirmDialog confirmDialog={confirmDialog} setConfirmDialog={setConfirmDialog} />}
            </MainCard>
        </>
    );
}
